import { BackButton, MainButton } from '@twa-dev/sdk/react'
import React, { useEffect, useState } from 'react'
import { useNavigate } from 'react-router-dom'

import axios from 'axios';
import { toast } from 'react-toastify';
import WebApp from '@twa-dev/sdk';

const Contacts = () => {
  const navigate = useNavigate()
  const [data, setData] = useState(null);
  const [loading, setLoading] = useState(true);

  useEffect(() => {
    axios
      .get('https://api.shashlichny-dom.ru/contacts.json')
      .then((response) => {
        setData(response.data);
      })
      .catch((error) => {
        console.error('Error fetching data:', error);
        toast.error('Ошибка загрузки данных.');
      })
      .finally(() => {
        setLoading(false);
      });
  }, []);

  const handleDataUpdate = (updatedItems) => {
    setData(prevData => ({
      ...prevData, updatedItems,
    }));
  };

  const saveData = () => {
    WebApp.HapticFeedback.impactOccurred('heavy');
    axios
    .put('https://api.shashlichny-dom.ru/api/save/contacts.json', data)  // Измените URL на /api/data
      .then(() => toast.success('Данные успешно обновлены!'))
      .catch((error) => {
        console.error('Error saving data:', error);
        toast.error('Ошибка обновления данных.');
      });
  };

  if (loading) return (
    <div className='w-full h-full flex justify-center items-center'>
      <span className='text-dark dark:text-white'>Загрузка...</span>
    </div>
  );

  const inputClassName = 'p-2 w-full border border-gray-300 focus:outline-none dark:border-gray dark:bg-dark dark:text-white rounded';

  return (
    <div className='w-full h-full flex flex-col justify-center items-center pt-4'>
      
      <div className='w-full flex flex-col pl-3'>
        <div className='flex items-center mb-3 w-full'>
          <div className='flex flex-col'>
            <span className='font-bold'>Телефон:</span>
            <input type="text" className={inputClassName} placeholder='Телефон' onChange={handleDataUpdate} value={data.phoneNumber || ''} />
          </div>
        </div>

        <div className='flex items-center mb-3 w-full'>
          <div className='flex flex-col'>
            <span className='font-bold'>Адрес:</span>
            <input type="text" className={inputClassName} placeholder='Адрес' onChange={handleDataUpdate} value={data.address || ''} />
          </div>
        </div>

        <div className='flex items-center mb-3 w-full'>
          <div className='flex flex-col'>
            <span className='font-bold'>Режим работы:</span>
            <input type="text" className={inputClassName} placeholder='Режим работы' onChange={handleDataUpdate} value={data.schedule || ''} />
          </div>
        </div>
      </div>

      <BackButton onClick={() => navigate('/admin-shd')} />
      <MainButton text='Сохранить изменения' onClick={saveData} />
    </div>
  )
}

export default Contacts